import {Images} from "../assets/images/images";
import {Link, NavLink} from "react-router-dom";
import {useState} from "react";
import NotesIcon from '@mui/icons-material/Notes';
import {Drawer} from "@mui/material";
import CloseIcon from '@mui/icons-material/Close';


export const Menu = () => {
    const [dropdown, setDropdown] = useState(false)
    const [state, setState] = useState({right: false});

    const activeCheck = ({ isActive, isPending }) => (
        isPending ? "pending" : isActive ? "active" : ""
    )

    const toggleDrawer = (anchor, open) => (event) => {
        if (event.type === 'keydown' && (event.key === 'Tab' || event.key === 'Shift')) {
            return;
        }
        setState({ ...state, [anchor]: open });
    };

    return(
        <div className={"menu"}>
            <div className={"container"}>
                <Link to={"/"}>
                    <img src={Images.longLogo} alt="logo"/>
                </Link>
                <div className={"menu__list"}>
                    <NavLink to={"/"} className={activeCheck}></NavLink>
                    <NavLink to={`/blogs/1`} className={activeCheck}></NavLink>
                    <NavLink to={`/events/1`} className={activeCheck}></NavLink>
                    <div className={"menu__list__dropdown"}>
                        <NavLink to={"/about"} onMouseEnter={() => setDropdown(!dropdown)}> </NavLink>
                        {
                            dropdown &&
                                <div className={"menu__list__dropdown__list"} onMouseLeave={() => setDropdown(!dropdown)}>
                                    <NavLink to={"/about"} className={activeCheck}> </NavLink>
                                    <NavLink to={"/products"} className={activeCheck}></NavLink>
                                    <NavLink to={"/portfolio"} className={activeCheck}></NavLink>
                                </div>
                        }
                    </div>
                    <NavLink to={"/contact"} className={activeCheck}></NavLink>
                </div>
                <NotesIcon className={"openBtn"} onClick={toggleDrawer("right", true)} func={toggleDrawer}/>
                <Drawer
                    anchor={"right"}
                    open={state.right}
                    onClose={toggleDrawer("right", false)}
                >
                    <div className={"mobMenu"}>
                        <CloseIcon className={"closeBtn"} onClick={toggleDrawer("right", false)}/>
                        <NavLink to={"/"} className={activeCheck}></NavLink>
                        <NavLink to={"/blogs/1"} className={activeCheck}></NavLink>
                        <NavLink to={"/events/1"} className={activeCheck}></NavLink>
                        <NavLink to={"/about"} className={activeCheck}> </NavLink>
                        <NavLink to={"/products"} className={activeCheck}></NavLink>
                        <NavLink to={"/portfolio"} className={activeCheck}></NavLink>
                        <NavLink to={"/contact"} className={activeCheck}></NavLink>
                    </div>
                </Drawer>
            </div>
        </div>
    )
}